import uniPopup from '@/components/uni-popup/uni-popup.vue';
// import uniTransition from '@/components/uni-transition/uni-transition.vue';
import viewHighlight from '@/components/view-highlight.vue';
import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';

import { mapState, mapMutations } from 'vuex';

export default {
	components: { uniPopup, viewHighlight, uniSegmentedControl },
	name: 'color-picker',
	props: {
		tabItems: {
			type: Array
		},
		colorSets: {
			type: Array
		},
		defColor: {
			type: String,
			default: 'transparent'
		},
		w: {
			type: Number,
			default: 50
		},
		h: {
			type: Number,
			default: 50
		},
		viewH: {
			type: Number,
			default: 600
		}
	},
	data() {
		return {
			maskClick: false,
			showColorPicker: false,
			color: 'transparent',
			style: '',
			hl: false,
			tabIndex: 0,
			transfromClass: {
				position: 'absolute',
				bottom: 0,
				top: 0,
				left: 0,
				right: 0,
				width: '100%',
				// height: 'auto',
				margin: 'auto',
				display: 'flex',
				'justify-content': 'center',
				// 'align-items': 'center',
				'background-color': 'black' // 'rgba(251, 251, 251, 0.9)'
			}
		};
	},
	methods: {
		getColor(c) {
			this.color = c;
		},
		onChangeColorSetTabItem: function(e) {
			if (this.tabIndex !== e.currentIndex) {
				this.tabIndex = e.currentIndex;
			}
		},
		transIsDone() {},
		onDone() {},
		onChangeTabItem(e) {
			if (this.tabIndex !== e.currentIndex) {
				this.tabIndex = e.currentIndex;
			}
		},
		submit() {
			this.$refs.popupColor.close();
			this.$emit('loadColorPicker', this.color);
		},
		openPopup(){
			this.$refs.popupColor.open();
		}
		
	}
};
